<template>
    <div>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="" prop="title">
            <el-input v-model="ruleForm.title"></el-input>
        </el-form-item>
        <el-form-item label="" prop="phone">
            <el-input v-model="ruleForm.phone"></el-input>
        </el-form-item>
        <el-form-item label="" prop="Identity">
            <el-select v-model="ruleForm.Identity" placeholder="">
                <el-option label="" value="passenger"></el-option>
                <el-option label="" value="driver"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="" required>
            <el-col :span="11">
                <el-form-item prop="date1">
                    <el-date-picker type="date" placeholder="" v-model="ruleForm.date1"
                                    style="width: 100%;"></el-date-picker>
                </el-form-item>
            </el-col>
            <el-col class="line" :span="2">-</el-col>
            <el-col :span="11">
                <el-form-item prop="date2">
                    <el-time-picker type="fixed-time" placeholder="" v-model="ruleForm.date2"
                                    style="width: 100%;"></el-time-picker>
                </el-form-item>
            </el-col>
        </el-form-item>
        <el-form-item label="" prop="desc">
            <el-input type="textarea" v-model="ruleForm.desc"></el-input>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
            <el-button @click="resetForm('ruleForm')"></el-button>
        </el-form-item>
    </el-form>
    </div>
</template>

<script>
    export default {
        name: "create",
        data() {
            return {
                ruleForm: {
                    title: '',
                    phone:'',
                    Identity: '',
                    date1: '',
                    date2: '',
                    desc: ''
                },
                rules: {
                    title: [
                        {required: true, message: '', trigger: 'blur'},
                        {min: 2, max: 8, message: ' 2  8 ', trigger: 'blur'}
                    ],
                    phone: [
                        {required: true, message: '', trigger: 'blur'},
                        {min: 8, max: 11, message: '', trigger: 'blur'}
                    ],
                    Identity: [
                        {required: true, message: '', trigger: 'change'}
                    ],
                    date1: [
                        {type: 'date', required: true, message: '', trigger: 'change'}
                    ],
                    date2: [
                        {type: 'date', required: true, message: '', trigger: 'change'}
                    ],
                    desc: [
                        {required: true, message: '', trigger: 'blur'}
                    ]
                }
            };
        },
        methods: {
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        let Blogs = this.$AV.Object.extend('Blogs');
                        let blogs = new Blogs();
                        let currentUser = this.$AV.User.current();
                        blogs.save({
                            title: this.ruleForm.title,
                            phone:this.ruleForm.phone,
                            Identity: this.ruleForm.Identity,
                            date1: this.ruleForm.date1,
                            date2: this.ruleForm.date2,
                            desc: this.ruleForm.desc,
                            user:currentUser,
                            userId:currentUser.toJSON().objectId,
                        }).then(()=>{
                            this.$notify({
                                title:'',
                                message:' ()"',
                                type:'success',
                                offset: 100
                            });
                            this.$router.push({
                                name:'Index',
                                page:1
                            });
                        })
                    } else {
                        return false;
                    }
                });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            }
        }
    }
</script>

<style scoped>
    .el-col-2{
        text-align: center;
    }
</style>